___CSS_LOADER_EXPORT___.push([module.id, `body {
  padding: 0px;
  margin: 0px;
  font-family: Arial, Helvetica, sans-serif;
}

#hero img {
  display: block;
  width: 100%;
}

footer {
  margin-top: 100px;
  text-align: center;
}

button,
a,
input,
textarea {
  min-width: 44px;
  min-height: 44px;
}

.skip-link {
  position: absolute;
  top: -80px;
  left: 10px;
  padding: 10px;
  background-color: #00ccff;
  color: white;
  font-weight: bold;
  text-decoration: none;
  border-radius: 5px;
  z-index: 1000;
  transition: top 0.5s ease-in-out;
}

.skip-link:focus {
  top: 10px;
  left: 10px;
  background-color: #1886fc;
}

.skip-link:active,
.skip-link:focus {
  top: 10px;
  left: 10px;
  transition: top 0.5s ease-in-out;
}

.loading-indicator {
  text-align: center;
  font-size: 20px;
  color: #1b1717;
  display: none;
  padding: 19px;
}

.error-message {
  text-align: center;
  font-size: 18px;
  color: red;
  display: none;
  padding: 19px;
}
